import React, { Component } from 'react';

import Tree from '../../lib/react-d3-tree.min';

import './App.css';

const treeData = [
  {
    name: 'Top Level',
    parent: 'null',
    attributes: {
      keyA: 'val A',
      keyB: 'val B',
      keyC: 'val C',
    },
    children: [
      {
        name: 'Level 2: A',
        parent: 'Top Level',
        attributes: {
          keyA: 'val A',
          keyB: 'val B',
          keyC: 'val C',
        },
        children: [
          {
            name: 'Level 3: Son of A',
            parent: 'Level 2: A',
            attributes: {
              keyA: 'val A',
              keyB: 'val B',
              keyC: 'val C',
            },
          },
          {
            name: 'Level 3: Daughter of A',
            parent: 'Level 2: A',
            attributes: {
              keyA: 'val A',
              keyB: 'val B',
              keyC: 'val C',
            },
          },
        ],
      },
      {
        name: 'Level 2: B',
        parent: 'Top Level',
      },
    ],
  },
];

const treeData2 = [
  {
    name: 'Top Level',
    parent: 'null',
    attributes: {
      keyA: 'val A',
      keyB: 'val B',
      keyC: 'val C',
    },
    children: [
      {
        name: 'Level 2: A',
        parent: 'Top Level',
        attributes: {
          keyA: 'val A',
          keyB: 'val B',
          keyC: 'val C',
        },
        children: [
          {
            name: 'Son of A',
            parent: 'Level 2: A',
            attributes: {
              keyA: 'val A',
              keyB: 'val B',
              keyC: 'val C',
            },
          },
          {
            name: 'Daughter of A',
            parent: 'Level 2: A',
            attributes: {
              keyA: 'val A',
              keyB: 'val B',
              keyC: 'val C',
            },
          },
          {
            name: 'Son of A',
            parent: 'Level 2: A',
          },
          {
            name: 'Daughter of A',
            parent: 'Level 2: A',
          },
        ],
      },
      {
        name: 'Level 2: B',
        parent: 'Top Level',
        children: [
          {
            name: 'Son of B',
            parent: 'Level 2: B',
          },
          {
            name: 'Daughter of B',
            parent: 'Level 2: B',
          },
        ],
      },
    ],
  },
];

class App extends Component {

  constructor() {
    super();
    this.state = {
      data: treeData,
      orientation: 'horizontal',
      pathFunc: 'diagonal',
      translateX: 200,
      translateY: 200,
      collapsible: true,
      initialDepth: undefined,
      zoomable: true,
    };
    this.setTreeData = this.setTreeData.bind(this);
    // this.setTreeDataFromCSV = this.setTreeDataFromCSV.bind(this);
    this.setOrientation = this.setOrientation.bind(this);
    this.setPathFunc = this.setPathFunc.bind(this);
    this.handleChange = this.handleChange.bind(this);
    this.toggleCollapsible = this.toggleCollapsible.bind(this);
    this.toggleZoomable = this.toggleZoomable.bind(this);
  }

  setTreeData(data) {
    this.setState({ data });
  }
  
  // setTreeDataFromCSV(csvFile) {
  //   treeUtil.parseCSV(csvFile)
  //   .then((data) => {
  //     console.log(data);
  //     this.setState({data})
  //   })
  //   .catch((err) => console.error(err));
  // }

  setOrientation(orientation) {
    this.setState({ orientation });
  }

  setPathFunc(pathFunc) {
    this.setState({ pathFunc });
  }

  handleChange(evt) {
    const target = evt.target;
    this.setState({
      [target.name]: parseInt(target.value, 10),
    });
  }

  toggleCollapsible() {
    this.setState((prevState) => 
      ({ collapsible: !prevState.collapsible })
    );
  }
  
  toggleZoomable() {
    this.setState(prevState => 
      ({ zoomable: !prevState.zoomable })
    )
  }

  render() {
    return (
      <div className="App">
        <div>
          Data Set:
          <button onClick={() => this.setTreeData(treeData)}>TreeData 1</button>
          <button onClick={() => this.setTreeData(treeData2)}>TreeData 2</button>
          {/* <button onClick={() => this.setTreeDataFromCSV('http://localhost:3000/MOCK_DATA.csv')}>CSV Data</button> */}
        </div>
        
        <div>
          Orientation:
          <button onClick={() => this.setOrientation('horizontal')}>{'Horizontal'}</button>
          <button onClick={() => this.setOrientation('vertical')}>{'Vertical'}</button>
        </div>
        
        <div>
          Path Func:
          <button onClick={() => this.setPathFunc('diagonal')}>{'Diagonal'}</button>
          <button onClick={() => this.setPathFunc('elbow')}>{'Elbow'}</button>
        </div>
        
        <div>
          <button onClick={this.toggleCollapsible}>
            {`Collapsible: ${this.state.collapsible}`}
          </button>
        </div>
        
        <div>
          <div>
            <label htmlFor="translateX">Translate X</label>
            <input
              name="translateX"
              type="number"
              defaultValue={this.state.translateX}
              onChange={this.handleChange}
            />
          </div>
          <div>
            <label htmlFor="translateY">Translate Y</label>
            <input
              name="translateY"
              type="number"
              defaultValue={this.state.translateY}
              onChange={this.handleChange}
            />
          </div>
        </div>
        
        <div>
          <label htmlFor="initialDepth">Initial depth on render</label>
          <input
            name="initialDepth"
            type="text"
            value={this.state.initialDepth || 'maximum'}
            readOnly
          />
        </div>
        
        <div>
          {`Zoomable: ${this.state.zoomable}`}
        </div>

        <Tree
          data={this.state.data}
          orientation={this.state.orientation}
          translate={{ x: this.state.translateX, y: this.state.translateY }}
          pathFunc={this.state.pathFunc}
          collapsible={this.state.collapsible}
          initialDepth={this.state.initialDepth}
          zoomable={this.state.zoomable}
        />
      </div>
    );
  }
}

export default App;



